.Title {
    text-align: center;
    background: url('/background.jpg');
    height: 100%;
}

.TitleShadow {
    background: -webkit-gradient(linear, left top, left bottom, from(#0D47A0), to(#0D47A000));
    background: -webkit-linear-gradient(top, #0D47A0, #0D47A000);
    background: -o-linear-gradient(top, #0D47A0, #0D47A000);
    background: linear-gradient(to bottom, #0D47A0, #0D47A000);
    height: 100%;
    color: #ffffff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.TitleTextContainer {
    background: url('/title-frame.png');
    background-size: 100%;
    background-repeat: no-repeat;
    width: 250px;
    height: 250px;
    display: -ms-flexbox;
    display: flex;
    margin: auto;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.TitleText {
    margin-top: 30px;
    font-size: 1.2rem;
}

.TitleGuideContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: #0006;
    width: 100%;
    height: 60px;
    margin: auto;
}



